import ListErrors from './ListErrors';
import React from 'react';
import { Link } from 'react-router';
import agent from '../agent';
import { connect } from 'react-redux';
import store from '../store';
import * as Actions from '../actions';

class SettingsForm extends React.Component {
  constructor() {
    super();

    this.state = {
      image: '',
      username: '',
      bio: '',
      email: '',
      password: ''
    };

    this.updateState = field => ev => {
      const state = this.state;
      const newState = Object.assign({}, state, { [field]: ev.target.value });
      this.setState(newState);
    };

    this.submitForm = ev => {
      ev.preventDefault();

      const user = {};
      if(this.state.image) user.image = this.state.image;
      if(this.state.username) user.username = (this.state.username).toLowerCase();
      if(this.state.bio) user.bio = this.state.bio;
      if(this.state.email) user.email = this.state.email;
      if(this.state.password) user.password = this.state.password;

      this.props.onSubmitForm(user, this.props.currentUser.username);
    };
  }

  componentWillMount() {
    if (this.props.currentUser) {
      Object.assign(this.state, {
        image: this.props.currentUser.image || '',
        username: this.props.currentUser.username,
        bio: this.props.currentUser.bio,
        email: this.props.currentUser.email
      });
    }
  }

  // componentWillUnmount() {
  //   this.props.onUnload();
  // }

  componentWillReceiveProps(nextProps) {
    if (nextProps.currentUser) {
      this.setState(Object.assign({}, this.state, {
        image: nextProps.currentUser.image || '',
        username: nextProps.currentUser.username,
        bio: nextProps.currentUser.bio,
        email: nextProps.currentUser.email
      }));
    }
  }

  render() {
    return (
      <form onSubmit={this.submitForm}>
        <fieldset>

          <fieldset className="form-group">
            <input
              className="form-control"
              type="text"
              placeholder="URL of profile picture"
              value={this.state.image}
              onChange={this.updateState('image')} />
          </fieldset>

          <fieldset className="form-group">
            <input
              className="form-control form-control-lg"
              type="text"
              placeholder="Username"
              value={this.state.username}
              onChange={this.updateState('username')} />
          </fieldset>

          <fieldset className="form-group">
            <textarea
              className="form-control form-control-lg"
              rows="8"
              placeholder="Short bio about you"
              value={this.state.bio}
              onChange={this.updateState('bio')}>
            </textarea>
          </fieldset>

          <fieldset className="form-group">
            <input
              className="form-control form-control-lg"
              type="email"
              placeholder="Email"
              value={this.state.email}
              onChange={this.updateState('email')} />
          </fieldset>

          <fieldset className="form-group">
            <input
              className="form-control form-control-lg"
              type="password"
              placeholder="New Password"
              value={this.state.password}
              onChange={this.updateState('password')} />
          </fieldset>

          <button
            className="btn btn-lg btn-primary pull-xs-right"
            type="submit"
            disabled={this.state.inProgress}>
            Update Settings
          </button>

        </fieldset>
      </form>
    );
  }
}

const mapStateToProps = state => ({
  ...state.settings,
  currentUser: state.common.currentUser
});

// const mapDispatchToProps = dispatch => ({
//   onClickLogout: () => dispatch({ type: 'LOGOUT' }),
//   onSubmitForm: user =>
//     dispatch({ type: 'SETTINGS_SAVED', payload: agent.Auth.save(user) }),
//   onUnload: () => dispatch({ type: 'SETTINGS_PAGE_UNLOADED' })
// });

class Settings extends React.Component {
  componentWillMount() {
    this.props.getProfileUser(this.props.currentUser.uid);
  }

  componentWillUnmount() {
    this.props.unloadSettings();
  }

  render() {
    return (
      <div className="roow roow-col-left page-common settings-page">
       
            <div className="settings-wrapper roow roow-col-left">

              <div className="page-title-wrapper roow">
                <div className="text-page-title">New Reccoon</div>
              </div>

              <ListErrors errors={this.props.errors}></ListErrors>

              <SettingsForm
                currentUser={this.props.firebaseUser}
                onSubmitForm={this.props.saveSettings} />

              <hr />

              <button
                className="btn btn-outline-danger"
                onClick={this.props.signOutUser}>
                Or click here to logout.
              </button>

            </div>

      </div>
    );
  }
}

// export default connect(mapStateToProps, mapDispatchToProps)(Settings);
export default connect(mapStateToProps, Actions)(Settings);


